import React, { Component } from 'react'
import FaPencil from 'react-icons/lib/fa/pencil'
import FaTrash from 'react-icons/lib/fa/trash'
import FaFloppyO from 'react-icons/lib/fa/floppy-o'

class Note extends Component {
	constructor(props) {
		super(props)

		this.state = {
			editing: false,
			style: {
				left: props.xCoord + 'px',
				top: props.yCoord + 'px',
				transform: 'rotate(' + props.rotate + 'deg)',
				zIndex: 0
			}
		}
		
		this.edit = this.edit.bind(this)
		this.remove = this.remove.bind(this)
		this.save = this.save.bind(this)
		this.click = this.click.bind(this)
		this.renderForm = this.renderForm.bind(this)
		this.renderText = this.renderText.bind(this)
	}

	componentWillReceiveProps(nextProps){
        if(nextProps.xCoord !== this.props.xCoord || nextProps.yCoord !== this.props.yCoord) {
            this.setState(prevState => ({
            	...prevState,
            	style: {
            		...prevState.style,
            		left: nextProps.xCoord,
            		top: nextProps.yCoord
            	}
            }))
            console.log("componentwillreceiveprops TRUE")
        }
    }

	shouldComponentUpdate(nextProps, nextState) {
		return (
			this.props.xCoord !== nextProps.xCoord ||
			this.props.yCoord !== nextProps.yCoord ||
			this.props.children !== nextProps.children || 
			this.state !== nextState
		)
	}

	componentDidUpdate() {
		var textArea
		if(this.state.editing) {
			textArea = this._newText
			textArea.focus()
			textArea.select()
		}
	}

	click(e) {
		e.stopPropagation()
		this.props.highestZ.raise()

		this.setState({
			...this.state,
			style: {
				...this.state.style,
				zIndex: this.props.highestZ.val
			}
		})
	}

	edit(e) {
		e.stopPropagation()

		this.props.highestZ.raise()

		this.setState({
			editing: true,
			style: {
				...this.state.style,
				zIndex: this.props.highestZ.val
			}
		})
	}

	remove() {
		this.props.onRemove(this.props.index)
	}

	save(e) {
		e.preventDefault()
		this.props.onChange(this._newText.value, this.props.index)
		this.setState({
			editing: false
		})
	}

	renderForm() {
		return (
			<div className="note" style={this.state.style} onClick={(e) => {e.stopPropagation()}}>
				<form onSubmit={this.save}>
					<textarea ref={input => this._newText = input}
							  defaultValue={this.props.children}/>
					<button id="save"><FaFloppyO size="20" /></button>
				</form>
			</div>
		)
	}

	renderText() {
		return (
			<div className="note" style={this.state.style} onClick={this.click}>
				<p>{this.props.children}</p>
				<span>
					<button onClick={this.edit} id="edit"><FaPencil size="20" /></button>
					<button onClick={this.remove} id="remove"><FaTrash size="20" /></button>
				</span>
			</div>
		)
	}

	render() {
		console.log("render note")
		return this.state.editing ? this.renderForm() : this.renderText()
	}

}

export default Note


