import React, { Component } from 'react'
import { withCookies, Cookies } from 'react-cookie';
import { instanceOf } from 'prop-types';
import Note from './Note'

class Board extends Component {
	static propTypes = {
    	cookies: instanceOf(Cookies).isRequired
  	}

	constructor(props) {
		super(props)
		
		this.clickBoard = this.clickBoard.bind(this)
		this.eachNote = this.eachNote.bind(this)
		this.update = this.update.bind(this)
		this.remove = this.remove.bind(this)
		this.nextId = this.nextId.bind(this)
		this.randomBetween = this.randomBetween.bind(this)
		this.highestZ = {
			val: 0,
			raise: function() {
				this.val++
			}
		}

		this.state = {
			notes: []
		}

		const { cookies } = this.props
		//const blah = cookies.remove('notes')
		const cookieNotes = cookies.get('notes') || [{ id: 0, x: 100, y: 100, rotate: -5, note: 'Sample Note'}]
		cookieNotes.forEach(
			item => {
				this.state.notes.push({
					id: this.nextId(),
					x: item.x,
					y: item.y,
					rotate: item.rotate,
					note: item.note
				})
			})
	}

	updateDimensions() {
		this.setState(prevState => ({
			notes: prevState.notes.map(
				note => ({...note, 
					x: Math.min(note.x, window.innerWidth - 150), 
					y: Math.min(note.y, window.innerHeight - 150)
				})
			)
		}))
	}

	componentDidMount() {
		this.updateDimensions();
		window.addEventListener("resize", this.updateDimensions.bind(this));
	}

	componentWillUnmount() {
		window.removeEventListener("resize", this.updateDimensions.bind(this));
	}

	componentDidUpdate() {
		/* Save changes to board into a cookie */
		const { cookies } = this.props;
		cookies.set('notes', JSON.stringify(this.state.notes), { path: '/', maxAge: 3600 });
	}

	nextId() {
		this.uniqueId = this.uniqueId || 0
		return this.uniqueId++
	}

	randomBetween(x, y) {
		return x + Math.ceil(Math.random() * (y-x))
	}

	clickBoard(e) {
		e.persist()

		this.setState(prevState => ({
			notes: [
				...prevState.notes,
				{
					id: this.nextId(),
					x: Math.min(e.clientX, window.innerWidth - 150),
					y: Math.min(e.clientY, window.innerHeight - 150),
					rotate: this.randomBetween(-15, 15),
					note: "New Note"
				}
			]
		}))
	}

	update(newText, i) {
		this.setState(prevState => ({
			notes: prevState.notes.map(
				note => (note.id !== i) ? note : {...note, note: newText}
			)
		}))
	}

	remove(id) {
		this.setState(prevState => ({
			notes: prevState.notes.filter(note => note.id !== id)
		}))
	}

	eachNote(note, i) {
		return (
			<Note key={note.id}
				  index={note.id}
				  xCoord={note.x}
				  yCoord={note.y}
				  rotate={note.rotate}
				  onChange={this.update}
				  onRemove={this.remove}
				  highestZ={this.highestZ}>
				  {note.note}
		    </Note>
		)
	}

	render() {
		return (
			<div className="board" onClick={this.clickBoard}>
				{this.state.notes.map(this.eachNote)}
			</div>
		)
	}
}

export default withCookies(Board)


